




















































	import mTitle from '@/components/m-title'
	import mVipCard from '@/components/m-vip-card'
	export default {
		components: {
			mTitle,
			mVipCard
		},
		data() {
			return {
				chooseVipId:0,
				isVip:false,//是否为会员
				myMember:{
					nickName:'',
					dueTime:'',
					memberType:'',
					discount:'',
					memberSynopsis:''
				},
				members:[],
				userData:{},
				vipName:"",
				vipDescribes:"" //会员卡权益
			};
		},
		methods:{
			getUser(){
				this.userData = JSON.parse(uni.getStorageSync('userData'));
				if(!this.userData.avatarUrl){
					this.$set(this.userData,'avatarUrl',this.userData.headAddress||'')
				}
				if(!this.userData.nickName){
					this.$set(this.userData,'nickName',this.userData.nickname||'')
				}
			},
			// 会员列表
			getVips(){
				let _this = this;
				this.mPost("/server/m/members",{}).then(res=>{
					if(res.code==1){
						_this.members=res.data.members
					}
				})
			},
			// 选择会员卡
			chooseVip(res){
				// console.log(res);
				this.chooseVipId=res.id,
				this.vieName=res.synopsis;
				this.vipDescribes=res.describes;
			},
			changeType(memberType){
				switch(memberType){
					case '0':
					return "月";
					case '1':
					return "季";
					case '2':
					return "半年";
					case '3':
					return "年";
				}
			},
			//我的会员
			myVips(){
				let _this = this;
				this.mPost("/server/m/myMember",{}).then(res=>{
					if(res.code==1){
						let data = res.data.myMember;
						if(data){
							// 会员
							_this.isVip = true
							if(data&&data['memberType']){
								data['memberType']= _this.changeType(data['memberType']);
							}
							if(data&&data['discount']){
								data['discount']=_this.accMul(data['discount'],10)+'折'
							}
							_this.myMember=data;
							
						}else{
							// 非会员
							_this.isVip = false
						}
					}
				})
			},
			//购买vip
			buyVipFn(){
				let _this = this;
				this.mPost("/server/m/buyMember",_this.chooseVipId).then(res=>{
					console.log(res);
					let data =res.data;
					if(data){
						let paydata = {
							provider: 'wxpay',
							timeStamp: data.timeStamp+'',
							nonceStr: data.nonceStr,
							package: data.package,
							signType: data.signType,
							paySign: data.paySign
						}
						uni.requestPayment({
							...paydata,
							success: function(res) {
								uni.showModal({
									title: '支付成功',
									content: '恭喜哦，你已成功成为会员用户',
									showCancel:false,
									confirmText:'完成',
									success: function (res) {
										if (res.confirm) {
											_this.initData()
// 											uni.setStorageSync('orderTab', 1);
// 											uni.switchTab({  
// 												url: '/pages/tabBar/order'  
// 											});
										} 
										else if (res.cancel) {
											_this.initData()
											// console.log('用户点击取消');
										}
									}
								});
							},
							fail: function(err) {
								console.log(err);
							}
						})
					}
					
				}).catch(err=>{
					console.log(err)
				})
			},
			initData(){
				this.getUser();
				this.getVips();
				this.myVips();
			}
		},
		onLoad(options){
			this.initData()
		}
	}
